<template>
    <el-row style="width: 990px; margin-bottom: 5%;margin-top: 5%; margin-left: 10%;">
      <el-col :span="16">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span></span>
            <el-button style="float: right; padding: 3px 0" type="text" @click="handleSaying()"></el-button>
          </div>
          <p></p>
          <p></p>
          <p></p>
          <p></p>
          <p>bug</p>
          <p>......</p>
          <el-input type="textarea" rows="7" v-model="saying"></el-input>
        </el-card>
      </el-col>
      <el-col :span="8">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span></span>
            <el-button style="float: right; padding: 3px 0" type="text" @click="handleScore()"></el-button>
          </div>
          <p>:-)</p>
          <el-form label-width="80px">
            <el-form-item label="">
              <el-rate v-model="value1" show-text></el-rate>
            </el-form-item>
            <el-form-item label="">
              <el-rate v-model="value2" show-text></el-rate>
            </el-form-item>
            <el-form-item label="">
              <el-rate v-model="value3" show-text></el-rate>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>
    </el-row>
</template>

<script>
  export default {
    name: "Feedback",
    data() {
      return {
        saying: "",
        value1: 0,
        value2: 0,
        value3: 0,
      }
    },
    methods: {
      handleSaying()
      {
        if(this.saying.length==0){
          this.$alert("", {
            confirmButtonText: ''
          });
        }
        else{
            this.$alert("", {
              confirmButtonText: ''
            });
            this.saying="";
        }
      },
      handleScore()
      {
        if(this.value1==0||this.value2==0||this.value3==0){
          this.$alert("", {
            confirmButtonText: ''
          });
        }
        else{
            this.$alert("", {
              confirmButtonText: ''
            });
            this.value1=0;
            this.value2=0;
            this.value3=0;
        }
      },
    }

  }
</script>

<style>
  .el-form-item {
    margin-top: 30px;
  }

  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 90%;
    background-color: #f4f8ff;
  }
</style>



